        // console.log("_rerender_output BamTracebackOutputView")
        this._reset_output()
        $(`<div class="output_text output_stream output_stderr" style="background: #fdd" dir="auto">
                <pre>${this.model.get('content')}</pre>
            </div>`).appendTo(this.$el);
